<template>
  <div style="margin:10px">
     <Button type="primary" style="margin-bottom:10px;" @click='backHome'>返回上一级</Button>
    <div>
      <div class='sec-header'>用户信息</div>
      <div class="sec-main">
        <div class="sec-main-avatar">
          <img :src="member.avatarurl" v-if="member.avatarurl" style="width: 80px;height: 80px;border-radius: 50%;vertical-align: middle"/>
          <div v-else style="width: 80px;display: inline-block;">
            <Avatar style="background-color: #3bc0c3;" icon="person" size="large" />
          </div>
          <span class="mem-name">{{member.isvip?'VIP会员 ':'普通用户 '}}{{member.nickname}}</span>
        </div>
        <!-- <div class='sec-main-detail'>
          <span>真实姓名:</span><span>{{member.realname||'无'}}</span>
        </div> -->
        <div class='sec-main-detail'>
          <span>电 &nbsp; &nbsp; &nbsp; 话:</span><span>{{member.telnumber||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>国 &nbsp; &nbsp; &nbsp; 家:</span><span>{{member.country||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>省 &nbsp; &nbsp; &nbsp; 份:</span><span>{{member.province||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>城 &nbsp; &nbsp; &nbsp; 市:</span><span>{{member.city||'无'}}</span>
        </div>
        <!-- <div class='sec-main-detail'>
          <span>归属店铺:</span><span>{{member.storename||'无'}}</span>
        </div> -->
        <!-- <div class='sec-main-detail'>
          <span>上级邀请人:</span><span>{{member.parentNickName||'无'}}</span>
        </div> -->
        <div class='sec-main-detail'>
          <span>邀&nbsp;&nbsp;请&nbsp;&nbsp;码:</span><span>{{member.invitecode||'无'}}</span>
        </div>
        <!-- <div class='sec-main-detail'>
          <span>登录日期:</span><span>{{member.createtime||'无'}}</span>
        </div> -->
        <div class='sec-main-detail'>
          <span>真实姓名:</span><span>{{member.realname||'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>身份证号:</span><span>{{member.idcard||'无'}}</span>
        </div>
        <!-- <div class='sec-main-detail'>
          <span>会员注册日期:</span><span>{{member.registertime||'无'}}</span>
        </div> -->
      </div>
    </div>
    <div v-if="member.account">
      <div class='sec-header'>
        <span>账户信息</span>
      </div>
      <div class="sec-main">
        <div class='sec-main-detail'>
          <span>奖金钱包:</span><span>{{member.storecode ?member.storecode +'元':'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>购物钱包:</span><span>{{member.storename ?member.storename +'元':'无'}}</span>
        </div>
        <div class='sec-main-detail'>
          <span>累计积分:</span><span>{{member.idcard?member.idcard+'元':'无'}}</span>
        </div>
        <!-- <div class='sec-main-detail'>
          <span>凡人奖章:</span><span>{{member.account.medal?member.account.medal:'无'}}</span>
        </div> -->
      </div>
    </div>
     <!-- <div>
      <div class='sec-header'>
        <span>奖金明细信息</span>
      </div>
      <div class="sec-main">
          <div  class="sec-main-avatar">
            <DatePicker type="datetimerange" placement="bottom-end" v-model="rewardForm.date"  split-panels placeholder='请选择时间' 
            style="width:350px" :confirm='false'></DatePicker>
          </div>
          <Table :columns="rewardColumn" :data="rewardData" border :loading="rewardLoading" ></Table>
          <div style="margin-top: 15px;overflow: hidden">
            <div style="float: left;"></div>
            <div style="float: right;">
                <Page :total="totalcount" :page-size="rewardForm.pagesize" :page-size-opts="[10,20,30,40]" show-elevator show-sizer
                placement="top" show-total @on-change='indexChange' @on-page-size-change='sizeChange'></Page>
            </div>
         </div>
      </div>
    </div> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      member: {},
      rewardData: [],
      rewardLoading: false,
      totalcount:0,
      rewardForm: {
        date:['',''],
        pageindex: 0,
        pagesize: 10
      },
      rewardColumn: [
        {
          title: "#",
          minWidth: 60,
          align: "center",
          render: (h, params) => {
            return (
              params.index +
              this.rewardForm.pageindex * this.rewardForm.pagesize +
              1
            );
          }
        },
        {
          title: "类型",
          minWidth: 100,
          align: "center",
          render:(h,params)=>{
              return !!params.row.isexpense?'支出':'收入'
          }
        },
        {
          title: "金额(元)",
          minWidth: 100,
          key: "amount",
          align: "center"
        },
        {
          title: "备注",
          key: "remark",
          minWidth: 200,
          align: "center"
        },
        {
          title: "时间",
          minWidth: 180,
          key: "createtime",
          align: "center"
        }
      ]
    };
  },
  watch:{
      rewardForm:{
          handler(newval){
             this.getReward();
          },
          deep:true,
      }
  },
  mounted() {
    let that = this;
    that.$sitemap.prvname = "用户管理";
    that.$sitemap.currname = "用户详情";
    that.getMemberDetail();
    // that.getReward();
  },
  methods: {
    backHome(){
        this.$router.go(-1);
    },
    //获取会员详情
    getMemberDetail() {
      let that = this;
      let data = { memid: this.$route.query.id };
      that.$http
        .post(this.$api.member_get, data)
        .then(res => {
          let resData = res.data.data;
          if (res.data.result == "1" && resData) {
            that.member = resData || {};
          } else {
            that.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          that.$Message.error("服务器异常");
        });
    },
    //格式奖金参数
    formatReward() {
      let params = {};
      Object.keys(this.rewardForm).forEach(key => {
        switch (key) {
          case "date":
            if (!!this.rewardForm.date[0] && !!this.rewardForm.date[1]) {
              params.begin = Date.parse(this.rewardForm.date[0]) / 1000;
              params.end = Date.parse(this.rewardForm.date[1]) / 1000;
            }
            break;
          default:
            params[key] = this.rewardForm[key];
            break;
        }
      });
      return {
          memid: this.$route.query.id,
          flowtype:1,
          ...params
      };
    },
    //得到奖金明细
    getReward() {
      this.rewardLoading = true;
      this.$http.post(this.$api.accountflow_get,this.formatReward()).then(res=>{
          let resData=res.data.data;
          if(resData&&res.data.result == "1"){
              this.rewardData=resData||[];
              this.totalcount=+res.data.totalcount;
          }else{
              this.$Message.error(res.data.msg);
          }
      }).catch(err=>{
          this.$Message.error("服务器异常");
      }).then(()=>{
          this.rewardLoading = false;
      }) 
    },
    indexChange(index) {
      this.rewardForm.pageindex = index - 1;
    },
    sizeChange(size) {
      this.rewardForm.pagesize = size;
    }
  }
};
</script>
<style scoped>
.sec-header {
  background-color: #f1f4f5;
  height: 40px;
  line-height: 40px;
  padding: 0 0 0 20px;
  color: #3bc0c3;
}
.sec-main {
  min-height: 100px;
  padding: 20px 30px;
}
.sec-main-detail {
  line-height: 30px;
}
.sec-main-avatar {
  margin-bottom: 10px;
  padding: 5px 0;
}
.sec-main-avatar > span:last-of-type {
  font-size: 16px;
  margin-left: 70px;
  font-weight: bold;
  vertical-align: middle;
}
.sec-main-detail > span:first-of-type {
  width: 150px;
}
.sec-main-detail span {
  display: inline-block;
  padding: 0 5px;
  height: 30px;
}
</style>



